.menu-block {
    position: relative;
}

.menu-block:hover .dropdown{
    display: block;
    opacity: 1;
}

.dropdown {
    position: absolute;
    padding-top: 1rem;
    top: 100%;  /* アバターの直下に配置 */
    left: var(--dynamic-left, 0);
    z-index: 3; /* 他の要素より前面に */

    opacity: 0;
    transform: translateY(10%);
    visibility: hidden;
    transition: opacity 0.3s ease, transform 0.3s ease-in-out, visibility 0s ease 0.3s;
}

.show {
    opacity: 1;  /* 完全に不透明に */
    transform: translateY(0%);
    visibility: visible;
    transition: opacity 0.3s ease, transform 0.3s ease-in-out, visibility 0s ease 0s;
}

.menu {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 16rem;
    padding: 0.6rem 0;
    border-radius: 0.6rem;
    overflow: hidden; /* マスクとして機能する */

    background-color: #fff;
    box-shadow: 
        0 0 0 0.1rem hsla(0, 0%, 0%, 0.08),
        0px 6px 16px 0px rgba(58, 51, 102, 0.15);
}

.menuItem {
    padding: 1rem 2rem;
    background-color: #fff;
    color: var(--G1, #2A2933);
    text-align: left;
    font-size: 1.6rem;
    cursor: pointer;
    z-index: 0;
}

.menuItem:hover {
    position: relative;
    color: #ffffff;
    background-color: #00000000;
}

.menuItem:hover::after {
    position: absolute;
    content: "";
    top: 0;
  	left: 0.5rem;
    right: 0.5rem;
    width: auto;
	height: 100%;
    z-index: -1;
    
    border-radius: 0.4rem;
    background-color: var(--Brand, #5741E7);
}

.menuItem:last-child {
    box-shadow: none;  /* 最後の項目は下線を消す */
}
